
.z-search {
  display: flex;
  padding: 10px 10px 5px 10px;

  .z-search__main {
    flex: 1;
    padding-right: 12px;

    .el-form-item--mini.el-form-item,
    .el-form-item--mini.el-form-item {
      margin-bottom: 5px;
    }
  }

  .z-search__ctrl {
    flex: 0 0 125px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    padding-bottom: 5px;

    .el-button:last-child {
      display: none;

      i {
        transition: transform 0.3s;
        transform: rotate(-90deg);
      }
    }
  }

  &.is-collpase {
    .z-search__ctrl {
      flex: 0 0 160px;
      .el-button:last-child {
        display: initial;
      }
    }

    &.collapse {
      .z-search__main {
        height: 33px;
        overflow: hidden;
      }

      .z-search__ctrl .el-button:last-child i {
        transform: rotate(0deg);
      }
    }
  }
}
